.carousel-cont {
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.65);
  width: 100vw;
  height: 100vh;
  z-index: 100;
}

.topbar {
  display: flex;
  justify-content: flex-end;
  padding-right: 20px;
  padding-top: 10px;
  padding-bottom: 10%;
  font-size: 20px;
  color: white;
}

.topbar:hover {
  cursor: pointer;
}

button {
  -webkit-appearance: none;
          appearance: none;
  -webkit-filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.1));
          filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.1));
  -webkit-transition: all .5s ease;
         filter: drop-shadow(0 5px 5px rgba(0, 0, 0, 0.1));
  -webkit-transition: all .5s ease;
          transition: all .5s ease;
  border: none;
  background: #FFD800;
  border-radius: 30px;
  text-transform: uppercase;
  box-sizing: border-box;
  padding: 15px 40px;
  font-weight: 400;
  font-size: 15px;
  font-weight: 600;
  color: #222222;
  letter-spacing: 0.1rem;
  cursor: cursor;
}

button:hover {
  color: #FFFFFF;
  background: #222222;
  cursor: pointer;
  -webkit-filter: drop-shadow(0 0 5px rgba(0, 0, 0, 0.2));
          filter: drop-shadow(0 0 5px rgba(0, 0, 0, 0.2));
}

.wrapper {
  max-width: 1335px;
  box-sizing: border-box;
  padding: 0 20px;
  margin: auto;
}

.wrapper a {
  display: inline-block;
  margin: 5px;
}

.wrapper a:first-child {
  margin-left: 0;
}

.slider-wrapper {
  position: relative;
  height: 60%;
  overflow: hidden;
}

.slide {
  /* height: 100vh; */
  height: 100%;
  background-size: cover !important;
}

.slide::before {
  content: '';
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  /* background: --webkit-gradient(linear, left top, left bottom, from(transparent), to(rgba(0, 0, 0, 0.9))); */
  /* background: linear-gradient(transparent, rgba(0, 0, 0, 0.9)); */
  bottom: 0;
  left: 0;
}

.previousButton, .nextButton {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	z-index: 10;
	width: 42px;
	height: 42px;
	text-indent: -9999px;
	cursor: pointer;
  color: #FFFFFF;
}

.previousButton {
  background: url('../../assets/images/right.svg') no-repeat center center / 40px;
}

.nextButton {
  background: url('../../assets/images/right.svg') no-repeat center center / 40px;
}

.previousButton:hover {
  background: url('../../assets/images/right.svg') no-repeat center center / 40px;
}

.nextButton:hover {
  background: url('../../assets/images/right.svg') no-repeat center center / 40px;
}

.previousButton {
  left: 10px;
  -webkit-transform: rotate(180deg) translateY(calc(50% + 0px));
  transform: rotate(180deg) translateY(calc(50% + 0px));
}

.previousButton:hover {
  /* left: -5px; */
  opacity: 0.5;
}

.nextButton {
  right: 10px;
}

.nextButton:hover {
  /* right: -5px; */
  opacity: 0.5;
}

.slider-content {
  text-align: center;
}

.slider-content img {
  height: 100%;
  width: auto;
}

.slider-content .inner {
  padding: 0 70px;
  box-sizing: border-box;
  position: absolute;
  width: 100%;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.slider-content .inner button{
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
}

.slider-content .inner h1 {
  font-weight: 900;
  margin: 0 auto;
  max-width: 80%;
  color: #FFFFFF;
  font-size: 64px;
  line-height: 1;
  font-family: 'Rubik';
}

.slider-content .inner p {
  color: #FFFFFF;
  line-height: 1.5;
  margin: 20px auto 30px;
  max-width: 640px;
  font-size: 16px;
  font-family: 'Nunito';
  text-align: left;
}

.slider-content section {
  position: absolute;
  bottom: 20px;
  left: 20px;
}

.slider-content section span {
  color: #FFFFFF;
}

.slider-content section span {
  color: rgba(255, 255, 255, 0.5);
  font-size: 12px;
  display: inline-block;
  text-align: left;
  line-height: 1.4;
  vertical-align: middle;
  margin-left: 10px;
}

.slider-content section img {
  width: 40px;
  height: 40px;
  border: solid 2px rgba(255, 255, 255, 0.5);
  border-radius: 100%;
  vertical-align: middle;
}

.slider-content section span strong {
  color: #FFFFFF;
  font-size: 14px;
  display: block;
}

@media (max-height: 500px) {
  .slider-wrapper, .slide {
    height: calc(100vh - 75px);
  }
}

@media (max-width: 640px) {
  .slider-wrapper {
    max-height: calc(80vh - 75px);
    /* height: auto; */
    width: 100%;
  }    
}

@media (max-height: 600px) {
  .slider-content .inner h1 {
    font-size: 32px;
  }    
}

@media (max-height: 600px) {
  .topbar {
    padding-top: 1px;
    padding-bottom: 0;
  }    
}

@media (max-width: 640px) {
  .slider-content .inner h1 {
    font-size: 32px;
  }
}

@media (max-width: 640px) {
  .carousel-cont {
    padding: 0;
  }
}

@media (max-width: 640px) {
  .slider-content {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

@media (max-width: 640px) {
  .slider-content img {
    height: auto;
    width: 100%;
  }
}